    --red: #b23535;
    --green: #38ad5f;
    --blue: #464ca5;
    --white: #eeeade;
    --black: #282828;


    --text-color: var(--black);
    --background-color: var(--white);
}

@media (prefers-color-scheme: dark) {
    :root {
        --text-color: var(--white);
        --background-color: var(--black);
    }
}

* {
    color: inherit;
    background-color: inherit;
    font-family: inherit;
}

